import * as slick from 'slick-carousel';


$(document).ready(function() {
    const heroSlider = () => {
        let slider = $('.hero .slider');
        slider.slick({
            arrows: false,
            dots: true,
            autoplay: true,
            autoplaySpeed: 10000,
        })
    }
    heroSlider()

    const menuSlider = () => {
        let sliderMenu = $('.menu__slider');
        sliderMenu.slick({
            dots: false,
            infinite: true,
            speed: 300,
            slidesToShow: 11,
            infinite: true,
        })
    }
    menuSlider()


    const offersSlider = () => {
        let slider = $('.offers__slider');
        slider.slick({
            dots: true,
            arrows: false,
            infinite: true,
            slidesToShow: 3,
            speed: 300,
            responsive: [{
                breakpoint: 1024,
                settings: "unslick"
            }]

        })
    }
    offersSlider()

    const destroySlider = () => {
        let sliderMenu = $('.menu__slider');
        sliderMenu.slick('unslick')
        sliderMenu.addClass('menu__row')
        sliderMenu.removeClass('menu__slider')

    }

    if (window.innerWidth <= 1024) {
        destroySlider()
    } else {
        let sliderMenu = $('.menu__row');
        sliderMenu.addClass('menu__slider')
    }

    let sliderMenu = $('.menu__slider') || $('.menu__row')
    $(window).on('resize', function() {
        if (window.innerWidth <= 1024) {
            destroySlider()
        } else if ($('.menu__row')) {
            sliderMenu.addClass('menu__slider')
            sliderMenu.removeClass('menu__row')
            menuSlider()
        }
    })
})